import React, { useState, useEffect } from 'react';
import { BrowserRouter as Router } from 'react-router-dom'; // Keep only Router here
import MainContent from './components/MainContent';
import { database, ref, get } from './firebase';
import './App.css';

const App = () => {
  const [isSidebarVisible, setSidebarVisible] = useState(true);
  const [firebaseConnected, setFirebaseConnected] = useState(false);
  const [loading, setLoading] = useState(true);

  const toggleSidebar = () => {
    setSidebarVisible(!isSidebarVisible);
  };

  useEffect(() => {
    const checkFirebaseConnection = async () => {
      try {
        const dbRef = ref(database, '/');
        const snapshot = await get(dbRef);
        if (snapshot.exists()) {
          setFirebaseConnected(true);
        } else {
          setFirebaseConnected(false);
        }
      } catch (error) {
        console.error('Firebase connection error:', error);
        setFirebaseConnected(false);
      } finally {
        setLoading(false);
      }
    };
    checkFirebaseConnection();
  }, []);

  return (
    <Router>
      <div className="app">
        <div className="app__body">
          <MainContent /> {/* MainContent handles routes now */}
        </div>
        <div className="firebase-status">
          {loading && 'Checking Firebase connection...'}
          {!loading && firebaseConnected && 'Firebase is connected!'}
          {!loading && !firebaseConnected && 'Failed to connect to Firebase.'}
        </div>
      </div>
    </Router>
  );
};

export default App;
