import React, { useState, useEffect } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { Link,useLocation  } from 'react-router-dom';

export default function SideBar({ isMenuOpen }) {
  const [selectedLink, setSelectedLink] = useState('/');
  const location = useLocation();

  // Update selected link based on current location
  useState(() => {
    setSelectedLink(location.pathname);
  }, [location.pathname]);

  return (
    <AnimatePresence>
      {isMenuOpen && (
        <motion.aside
          initial={{ width: 0, opacity: 0 }}
          animate={{ width: 240, opacity: 1 }}
          exit={{ width: 0, opacity: 0 }}
          transition={{ duration: 0.3 }}
          className="fixed top-16 left-0 bottom-0 z-40 w-60 bg-white dark:bg-gray-800 overflow-y-auto"
        >
          <div className="px-2 py-4 space-y-2">
            <Link
              to="/"
              className={`flex items-center px-2 py-2 text-base font-medium rounded-md 
        ${selectedLink === '/' ? 'bg-blue-100 dark:bg-blue-900 text-blue-600 dark:text-blue-300' : 'text-gray-600 dark:text-gray-300'} 
        hover:bg-gray-50 dark:hover:bg-gray-700`}
              onClick={() => setSelectedLink('/')}
            >
              <svg className="mr-4 h-6 w-6 text-gray-500 dark:text-gray-400" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M3 12l2-2m0 0l7-7 7 7M5 10v10a1 1 0 001 1h3m10-11l2 2m-2-2v10a1 1 0 01-1 1h-3m-6 0a1 1 0 001-1v-4a1 1 0 011-1h2a1 1 0 011 1v4a1 1 0 001 1m-6 0h6" />
              </svg>
              Home
            </Link>

            <Link
              to="/profile"
              className={`flex items-center px-2 py-2 text-base font-medium rounded-md 
        ${selectedLink === '/profile' ? 'bg-blue-100 dark:bg-blue-900 text-blue-600 dark:text-blue-300' : 'text-gray-600 dark:text-gray-300'} 
        hover:bg-gray-50 dark:hover:bg-gray-700`}
              onClick={() => setSelectedLink('/profile')}
            >
              <svg className="mr-4 h-6 w-6 text-gray-400 dark:text-gray-500" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M14.752 11.168l-3.197-2.132A1 1 0 0010 9.87v4.263a1 1 0 001.555.832l3.197-2.132a1 1 0 000-1.664z" />
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M21 12a9 9 0 11-18 0 9 9 0 0118 0z" />
              </svg>
              Profile
            </Link>

            <Link
              to="/play"
              className={`flex items-center px-2 py-2 text-base font-medium rounded-md 
        ${selectedLink === '/play' ? 'bg-blue-100 dark:bg-blue-900 text-blue-600 dark:text-blue-300' : 'text-gray-600 dark:text-gray-300'} 
        hover:bg-gray-50 dark:hover:bg-gray-700`}
              onClick={() => setSelectedLink('/play')}
            >
              <svg className="mr-4 h-6 w-6 text-gray-400 dark:text-gray-500" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 8v4l3 3m6-3a9 9 0 11-18 0 9 9 0 0118 0z" />
              </svg>
              Video
            </Link>
          </div>
        </motion.aside>
      )}
    </AnimatePresence>
  )
}
