import React, { useState, useEffect } from 'react';
import { Routes, Route } from 'react-router-dom';
import { motion, AnimatePresence } from 'framer-motion';
import SearchContent from './SearchContent';
import SideBar from './SideBar';
import NavBar from './NavBar';
import Home from './Home';
import Player from './Player'; // Import the Player component
import Profile from './Profile'; // Import the Profile component

const videos = [
    { id: 1, title: 'Advanced React Hooks Tutorial', channel: 'React Mastery', views: '1.2M views', timestamp: '2 weeks ago', thumbnail: 'https://www.cibaky.com/wp-content/uploads/2015/12/placeholder-2.jpg' },
    { id: 2, title: 'Building Scalable APIs with GraphQL', channel: 'Backend Wizards', views: '890K views', timestamp: '1 month ago', thumbnail: 'https://www.cibaky.com/wp-content/uploads/2015/12/placeholder-2.jpg' },
    { id: 3, title: 'Machine Learning Fundamentals', channel: 'AI Explorers', views: '2.5M views', timestamp: '3 months ago',thumbnail: 'https://www.cibaky.com/wp-content/uploads/2015/12/placeholder-2.jpg' },
    { id: 4, title: 'Responsive Web Design Techniques', channel: 'CSS Ninjas', views: '1.8M views', timestamp: '2 months ago', thumbnail: 'https://www.cibaky.com/wp-content/uploads/2015/12/placeholder-2.jpg' },
    { id: 5, title: 'Blockchain Technology Explained', channel: 'Crypto Academy', views: '3.1M views', timestamp: '1 week ago',thumbnail: 'https://www.cibaky.com/wp-content/uploads/2015/12/placeholder-2.jpg' },
    { id: 6, title: 'Advanced TypeScript Patterns', channel: 'TypeScript Guru', views: '750K views', timestamp: '3 days ago',thumbnail: 'https://www.cibaky.com/wp-content/uploads/2015/12/placeholder-2.jpg' },
    { id: 7, title: 'Next.js 13 Crash Course', channel: 'Web Dev Simplified', views: '500K views', timestamp: '5 days ago', thumbnail: 'https://www.cibaky.com/wp-content/uploads/2015/12/placeholder-2.jpg' },
    { id: 8, title: 'Docker for Beginners', channel: 'DevOps Masters', views: '2.3M views', timestamp: '1 month ago', thumbnail: 'https://www.cibaky.com/wp-content/uploads/2015/12/placeholder-2.jpg' },
];

const categories = ['All', 'React', 'JavaScript', 'TypeScript', 'Node.js', 'Python', 'Machine Learning', 'Web Development', 'Mobile Development', 'DevOps', 'Data Science'];

const MainContent = () => {
    const [isMenuOpen, setIsMenuOpen] = useState(false);
    const [isDarkMode, setIsDarkMode] = useState(false);
    const [searchQuery, setSearchQuery] = useState('');
    const [selectedCategory, setSelectedCategory] = useState('All');
    const [showSearchResults, setShowSearchResults] = useState(false);

    useEffect(() => {
        const darkModeMediaQuery = window.matchMedia('(prefers-color-scheme: dark)');
        setIsDarkMode(darkModeMediaQuery.matches);

        const handleChange = (e) => setIsDarkMode(e.matches);
        darkModeMediaQuery.addEventListener('change', handleChange);

        return () => darkModeMediaQuery.removeEventListener('change', handleChange);
    }, []);

    return (
        <div className={`min-h-screen ${isDarkMode ? 'dark bg-gray-900' : ''}`}>
            <div className="text-gray-900 dark:text-white">

                <NavBar
                    setIsMenuOpen={setIsMenuOpen}
                    isMenuOpen={isMenuOpen}
                    searchQuery={searchQuery}
                    setSearchQuery={setSearchQuery}
                    setShowSearchResults={setShowSearchResults}
                    setIsDarkMode={setIsDarkMode}
                    isDarkMode={isDarkMode}
                />
                <SideBar isMenuOpen={isMenuOpen} />

                <main className={`pt-16 ${isMenuOpen ? 'ml-60' : ''} transition-margin duration-300 h-full`}>
                    <Routes>
                        <Route path="/" element={
                            showSearchResults ? (
                                <SearchContent
                                    videos={videos}
                                    searchQuery={searchQuery}
                                    showSearchResults={showSearchResults}
                                    setShowSearchResults={setShowSearchResults}
                                />
                            ) : (
                                <Home
                                    categories={categories}
                                    setSelectedCategory={setSelectedCategory}
                                    selectedCategory={selectedCategory}
                                    videos={videos}
                                    isDarkMode={isDarkMode}
                                />
                            )
                        } />
                        <Route path="/play" element={
                            showSearchResults ? (
                                <SearchContent
                                    videos={videos}
                                    searchQuery={searchQuery}
                                    showSearchResults={showSearchResults}
                                    setShowSearchResults={setShowSearchResults}
                                />
                            ) : (
                                <Player />
                            )


                        } />
                        <Route path="/profile" element={
                            showSearchResults ? (
                                <SearchContent
                                    videos={videos}
                                    searchQuery={searchQuery}
                                    showSearchResults={showSearchResults}
                                    setShowSearchResults={setShowSearchResults}
                                />
                            ) : (
                                <Profile />
                            )
                        } />
                    </Routes>
                </main>
            </div>
        </div>
    );
};

export default MainContent;
