import React, { useState, useEffect } from 'react';
import { motion, AnimatePresence } from 'framer-motion';

export default function Home({categories,setSelectedCategory,selectedCategory,videos,isDarkMode}) {
  return (
    <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
    {/* Categories */}
    <div className="mb-8 overflow-x-auto">
      <div className="flex space-x-4 pb-4">
        {categories.map((category) => (
          <button
            key={category}
            onClick={() => setSelectedCategory(category)}
            className={`w-full px-3 py-1 rounded-full text-sm font-medium ${
              selectedCategory === category
                ? 'bg-gray-900 text-white dark:bg-white dark:text-gray-900'
                : 'bg-gray-200 text-gray-900 dark:bg-gray-700 dark:text-white hover:bg-gray-300 dark:hover:bg-gray-600'
            }`}
          >
            {category}
          </button>
        ))}
      </div>
    </div>

    {/* Video grid */}
    <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4 gap-6">
      {videos.map((video) => (
        <>
        <motion.div 
              key={video.id}
              className={`${isDarkMode ? 'bg-gray-800' : ''}  rounded-lg overflow-hidden shadow-lg`}
              whileHover={{ scale: 1.05 }}
              transition={{ duration: 0.2 }}>
            <div class="relative">
                <img src={video.thumbnail}  alt={video.title} class="w-full h-48 object-cover"/>
                <div class="absolute bottom-2 right-2 bg-black bg-opacity-75 text-white text-xs px-2 py-1 rounded">3:02</div></div>
                <div class="p-4"><h3 class="text-lg font-semibold mb-2 line-clamp-2">{video.title}</h3>
                <p className="text-sm text-gray-600 dark:text-gray-400">{video.channel}</p>
                <p class="text-sm text-gray-400">{video.views}  {video.timestamp}</p>
            </div>
        </motion.div>
        </>
      ))}
    </div>
      </div>
  )
}
